<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb
      style="margin-bottom: 20px"
      separator-class="el-icon-arrow-right"
    >
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>学生管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-input placeholder="请输入内容" v-model="input">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
        <el-col :span="18">
          <el-button type="primary" @click="addstu('add')"
            >添加学生</el-button
          ></el-col
        >
      </el-row>
      <el-table
        border
        stripe
        :data="tabledata"
        style="width: 100%; margin-top: 20px"
      >
        <el-table-column prop="sname" label="姓名"> </el-table-column>
        <el-table-column prop="stu_age" label="年龄"> </el-table-column>
        <el-table-column prop="stu_attd" label="地址"> </el-table-column>
        <el-table-column prop="stu_birth" label="出生日期"> </el-table-column>
        <el-table-column prop="stu_yx" label="院系"> </el-table-column>
        <el-table-column prop="stu_zy" label="专业"> </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="edit(scope.row)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="del(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        style="margin-top: 20px; float: right; margin-bottom: 20px"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="1"
        :page-sizes="[5, 10, 15]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="4"
      >
      </el-pagination>
    </el-card>
    <!-- 添加学生对话框 -->
    <el-dialog
      :title="this.showStu === 'add' ? '添加学生' : '编辑学生'"
      :visible.sync="dialogVisibleStu"
      width="30%"
      @close="closeStu"
    >
      <el-form
        ref="formStu"
        :model="formStu"
        label-width="100px"
        :rules="rulesStu"
      >
        <el-form-item label="用户名" prop="sname">
          <el-input v-model="formStu.sname" style="width: 70%"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="spwd">
          <el-input v-model="formStu.spwd" style="width: 70%"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="stu_sex">
          <el-input v-model="formStu.stu_sex" style="width: 70%"></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="stu_age">
          <el-input
            v-model="formStu.stu_age"
            type="number"
            min="0"
            style="width: 70%"
          ></el-input>
        </el-form-item>
        <el-form-item label="地址" prop="stu_attd">
          <el-input v-model="formStu.stu_attd" style="width: 70%"></el-input>
        </el-form-item>
        <el-form-item label="出生日期" prop="stu_birth">
          <el-input
            type="date"
            v-model="formStu.stu_birth"
            style="width: 70%"
          ></el-input>
        </el-form-item>
        <el-form-item label="院系" prop="stu_yx">
          <el-input v-model="formStu.stu_yx" style="width: 70%"></el-input>
        </el-form-item>
        <el-form-item label="专业" prop="stu_zy">
          <el-input v-model="formStu.stu_zy" style="width: 70%"></el-input>
        </el-form-item>
        <el-form-item label="班级" prop="stu_bj">
          <el-input v-model="formStu.stu_bj" style="width: 70%"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleStu = false">取 消</el-button>
        <el-button type="primary" @click="StuBtn">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'MyStudent',
  data() {
    return {
      // 搜索框
      input: '',
      // table数据
      tabledata: [],
      // 学生对话框显示与隐藏
      dialogVisibleStu: false,
      // 添加学生表单
      formStu: {
        sname: '',
        spwd: '',
        stu_sex: '',
        stu_age: '',
        stu_attd: '',
        stu_birth: '',
        stu_bj: '',
        stu_yx: '',
        stu_zy: ''
      },
      rulesStu: {
        sname: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        spwd: [{ required: true, message: '请输入密码', trigger: 'blur' }],
        stu_sex: [{ required: true, message: '请输入性别', trigger: 'blur' }],
        stu_age: [{ required: true, message: '请输入年龄', trigger: 'blur' }],
        stu_attd: [{ required: true, message: '请输入地址', trigger: 'blur' }],
        stu_birth: [
          { required: true, message: '请输入出生日期', trigger: 'blur' }
        ],
        stu_bj: [{ required: true, message: '请输入班级', trigger: 'blur' }],
        stu_yx: [{ required: true, message: '请输入院系', trigger: 'blur' }],
        stu_zy: [{ required: true, message: '请输入专业', trigger: 'blur' }]
      },
      showStu: ''
    }
  },
  methods: {
    async getstudent() {
      const { data: res } = await this.$http.get('/student')
      this.tabledata = res.data
      // console.log(res.data)
    },
    // 编辑信息按钮事件函数
    edit(val) {
      this.dialogVisibleStu = true
      this.formStu = val
      console.log(val)
    },
    // 删除信息按钮事件函数
    async del(val) {
      console.log(val)
      const flag = await this.$confirm(
        // 提示信息
        '此操作将永久删除该学生信息, 是否继续?',
        '提示',
        {
          // 确定按钮文本内容
          confirmButtonText: '确定',
          // 取消按钮文本内容
          cancelButtonText: '取消',
          // 显示图标
          type: 'warning'
        }
        // catch捕获错误信息
      ).catch((err) => err)
      if (flag !== 'confirm') return this.$message.info('删除操作取消！')
    },
    // 添加学生信息
    addstu(val) {
      this.showStu = val
      this.dialogVisibleStu = true
    },
    // 添加学生对话框关闭事件函数
    closeStu() {
      // this.$refs.formStu.resetFields()
    },
    // 添加学生确定按钮事件函数
    StuBtn() {
      this.$refs.formStu.validate(async (valid) => {
        if (!valid) return this.$message.error('验证失败！')
        const { data: res } = await this.$http.post('/student/add', {
          user: '张三'
        })
        console.log(res)
      })
      this.dialogVisibleStu = false
    },
    // 每页条数
    handleSizeChange() {},
    // 当前页码
    handleCurrentChange() {}
  },
  created() {
    this.getstudent()
  }
}
</script>
<style lang="less" scoped></style>
